import React from 'react';
import Header from '@/components/Header';
import Footer from '@/components/Footer';
import CategoriesGrid from '@/components/CategoriesGrid';
import RecipeCard from '@/components/RecipeCard';
import recipes from '@/../public/recipes.json';

export const metadata = {
  title: 'Categorias de Receitas - Um Conto de Bolos',
  description: 'Explore receitas de bolos organizadas por categorias encantadas.',
};

export default function CategoriasPage() {
  const categories = Array.from(
    new Set(recipes.recipes.flatMap((r) => r.category))
  );

  return (
    <div className="min-h-screen flex flex-col bg-white">
      <Header />

      {/* Hero */}
      <section className="py-16 px-4 text-center bg-gradient-to-r from-pink-100 to-amber-100">
        <h1 className="text-5xl font-bold text-amber-900 mb-4">üé≠ Categorias Encantadas</h1>
        <p className="text-xl text-amber-800">
          Explore {categories.length} categorias de receitas para todas as ocasi√µes
        </p>
      </section>

      <main className="flex-1 py-16 px-4">
        <div className="max-w-7xl mx-auto">
          <CategoriesGrid />
        </div>
      </main>

      <Footer />
    </div>
  );
}
